*
    box-sizing: border-box
    ::-moz-selection 
        color: lightblue
    ::selection 
        color: lightblue
    -webkit-tap-highlight-color: transparent


html
    width: 100%
    height: 100%
    display: grid
    align-content: center

body
    border-radius: 15px
    display: grid
    align-content: center
    background-color: black
    background-image: url(/images/skyBG.jpg)
    background-repeat: no-repeat
    background-size: cover
    box-sizing: border-box
    height: auto
    overflow-x: hidden
    overflow-y: hidden
    margin: auto auto
    width: 100%
    min-width: 768px
    max-width: 1366px
    box-shadow: 10px 10px 10px 2px rgba(0,0,0,0.5), -10px 10px 10px 2px rgba(0,0,0,0.5), 10px -10px 10px 2px rgba(0,0,0,0.5), -10px -10px 10px 2px rgba(0,0,0,0.5)
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale


@media screen and (max-width: 480px)
    body
        position: absolute
        top: 40%
    

    
.App
    // margin-top: 25px
    // z-index: 2
    height: auto
    border: 2px solid black
    display: grid
    align-content: center
    background-color: black
    // transform: scaleY(.7)
    // position: relative
    // top: 20%
    // overflow-y: auto
@media screen and (max-device-width: 992px)
    body
        overflow-y: auto
@media screen and (max-device-width: 768px)
    body
        overflow-y: auto
        margin-top: 25px
    .App
        height: auto
@media screen and (max-width: 768px)
    body
        background-image: none
        background-color: black

.mainContentAndSides
    display: grid
    grid-template-columns: 15% 70% 15%

.fade-enter
    opacity: 0.01

.fade-enter.fade-enter-active
    opacity: 1
    transition: opacity 500ms ease-in


.fade-leave
    opacity: 1

.fade-leave.fade-leave-active
    opacity: 0.01
    transition: opacity 250ms ease-in

.video__media
    height: auto
    width: 100%
    position: absolute
    z-index: -5
    top: 0
    left: 0